  -webkit-text-size-adjust: 100%
}

body {
  margin: 0
}

article, aside, cite, code, details, figcaption, figure, footer, header, hgroup, main, nav, section, small, summary {
  display: block
}

article, article ol, article ul, blockquote, body, div, figure, footer, header, input, nav, section {
  box-sizing: border-box
}

audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline
}

