.wrapper {
  max-width: 1070px;
  margin-left: auto;
  margin-right: auto
}

.wrapper:after, .wrapper:before {
  content: "";
  display: table
}

.wrapper:after {
  clear: both
}

.content {
  width: calc(99.9% * 2/3 - 10px)
}

.content:nth-child(1n) {
  float: left;
  margin-right: 30px;
  clear: none
}

.content:last-child {
  margin-right: 0
}

.content:nth-child(3n) {
  margin-right: 0;
  float: right
}

.content:nth-child(3n+1) {
  clear: both
}

.main-inner {
  padding: 40px 10px
}

@media screen and (max-width:1100px) {
  .content {
    width: calc(99.9% * 2/4 - 15px)
  }
  .content:nth-child(1n) {
    float: left;
    margin-right: 0px;
    clear: none
  }
  .content:last-child {
    margin-right: 0
  }
  .content:nth-child(4n) {
    margin-right: 0;
    float: right
  }
  .content:nth-child(4n+1) {
    clear: both
  }
  .main-inner {
    padding: 35px 20px
  }
}

@media screen and (max-width:900px) {
  .content {
    width: calc(99.9% * 7/12 - 12.5px)
  }
  .content:nth-child(1n) {
    float: left;
    margin-right: 0px;
    clear: none
  }
  .content:last-child {
    margin-right: 0
  }
  .content:nth-child(12n) {
    margin-right: 0;
    float: right
  }
  .content:nth-child(12n+1) {
    clear: both
  }
  .main-inner {
    padding: 30px 20px
  }
}

@media screen and (max-width:500px) {
  .content {
    width: calc(99.9% * 4/4 - 0px)
  }
  .content:nth-child(1n) {
    float: left;
    margin-right: 30px;
    clear: none
  }
  .content:last-child {
    margin-right: 0
  }
  .content:nth-child(4n) {
    margin-right: 0;
    float: right
  }
  .content:nth-child(4n+1) {
    clear: both
  }
