    margin-right: 0
  }
  .sidebar:nth-child(12n) {
    margin-right: 0;
    float: right
  }
  .sidebar:nth-child(12n+1) {
    clear: both
  }
  .sidebar .sidebar-inner {
    padding: 30px 20px 0
  }
}

@media (max-width:500px) {
  .sidebar {
    width: calc(99.9% * 1 - 0px)
  }
  .sidebar:nth-child(1n) {
    float: left;
    margin-right: 30px;
    clear: none
  }
  .sidebar:last-child {
    margin-right: 0
  }
  .sidebar:nth-child(undefinedn) {
    margin-right: 0;
    float: right
  }
  .sidebar:nth-child(undefinedn+1) {
    clear: both
  }
  .sidebar .sidebar-inner {
    padding: 25px 20px 0
  }
  .sidebar .sidebar-inner:after {
    display: none
  }
}

.blog-page {
  margin-bottom: 40px
}

.blog-page h1 {
  font-size: calc(20px + 16 * ((100vw - 420px) / 860));
  margin-top: 0
}

@media screen and (min-width:1280px) {
  .blog-page h1 {
    font-size: 36px
  }
}

@media screen and (max-width:420px) {
  .blog-page h1 {
    font-size: 20px
  }
}

.blog-page p {
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 26px
}

@media (max-width:900px) {
  .blog-single .footer, .blog-single .text {
    padding: 0 15px
  }
}
